@import url("https://fonts.googleapis.com/css?family=Abril+Fatface");

.tabMenu {
  margin: auto;
  height: 60px;
  width: 700px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.buttons {
  width: 150px;
  margin: 0px;
  padding: 20px;
  font-family: century gothic;
  font-size: 20px;
  border: none;
  background: none;
}

.buttons:hover {
  font-weight: bold;
  cursor: pointer;
}

.contentContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.content {
  margin: auto;
  border: solid 1px lavender;

  box-shadow: 7px 7px lavender;
  padding: 20px;
  width: 700px;
  height: 400px;
  background-color: white;
}
