.aboutContainer {
  display: flex;
  font-family: century gothic;
  height: 100%;
}

a.leftButtons {
  margin: 0px;
}

.description {
  margin-top: 30px;
  width: 310px;
  height: 100px;
  text-align: justify;
  font-size: 18px;
  animation: animated-logo 1s 4s normal both;
}

.header {
  font-family: Courier, monospace;
  display: inline-block;
}

.header {
  border-right: solid 3px rgb(10, 231, 180);
  white-space: nowrap;
  overflow: hidden;
  font-family: monospace;
  font-size: 40px;
  color: (247, 229, 229, 0.7);
}

/* Animation */
.header {
  animation: animated-text 3s steps(14, end) 0.5s 1 normal both,
    animated-cursor 400ms steps(14, end) infinite;
}

/* text animation */

@keyframes animated-text {
  from {
    width: 0;
  }
  to {
    width: 310px;
  }
}

/* cursor animations */

@keyframes animated-cursor {
  from {
    border-right-color: rgb(10, 231, 180);
  }
  to {
    border-right-color: transparent;
  }
}

.rightComponent {
  display: flex;
  align-items: center;
  animation: animated-logo 1s 5s normal both;
}

.leftComponent {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 60%;
}

.buttonContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  animation: animated-logo 1s 5s normal both;
}

.leftButtons {
  text-decoration: none;
  text-align: center;
  align-content: center;
  margin: 20px 20px 10px 0px;
  padding: 5px;
  width: 120px;
  height: 30px;
  font-size: 18px;
  border: none;
  font-family: century gothic;
  box-shadow: 2px 2px gray;
  background-color: rgb(178, 178, 235);
  color: white;
}

.leftButtons:hover {
  border: solid 1px rgb(178, 178, 235);
  box-shadow: 2px 2px rgb(178, 178, 235);
  background-color: white;
  color: rgb(178, 178, 235);
  cursor: pointer;
}

.mailMe {
  font-family: "Courier New", Courier, monospace;
  font-size: 15px;
  padding-top: 10px;
  margin: 0px;
  white-space: nowrap;
  overflow: hidden;
  font-family: monospace;
}
