.logoContainer {
  display: flex;
  justify-content: space-around;
  margin-top: 50px;
}

.anotherLogoContainer {
  position: relative;
  margin: 0px;
  animation: animated-logo 0.5s 1s normal both;
}

.skillsLogo {
  display: block;
  width: 150px;
  height: 150px;
  transform: scale(1);
  transition: opacity ease-out 200ms;
}

.skillsText {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: opacity ease-out 250ms;
  font-size: 40px;
  font-family: century gothic;
  font-weight: bold;
}

.skillsLogo:hover {
  opacity: 0.5;
  cursor: pointer;
  animation: animated-skill-logo 1s;
  transform: scale(1.5);
  transition: 0.5s;
}

.anotherLogoContainer:hover .skillsText {
  opacity: 1;
}

.skillsHeader {
  font-family: Courier, monospace;
  display: inline-block;
  /*---*/
  border-right: solid 3px rgb(10, 231, 180);
  white-space: nowrap;
  overflow: hidden;
  font-family: monospace;
  font-size: 40px;
  color: (247, 229, 229, 0.7);
  /*animation*/
  animation: animated-text-skills 1s steps(6, end) 0s 1 normal both,
    animated-cursor 400ms steps(6, end) infinite;
}

@keyframes animated-text-skills {
  from {
    width: 0;
  }
  to {
    width: 135px;
  }
}

@keyframes animated-logo {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
