.educationHeader {
  font-family: Courier, monospace;
  display: inline-block;
  font-weight: bold;
  /*---*/
  border-right: solid 3px rgb(10, 231, 180);
  white-space: nowrap;
  overflow: hidden;
  font-family: monospace;
  font-size: 40px;
  color: (247, 229, 229, 0.7);
  /*animation*/
  animation: animated-text-education 2s steps(9, end) 0s 1 normal both,
    animated-cursor 400ms steps(9, end) infinite;
}

.educationContent {
  margin: 20px;
  display: flex;
  flex-direction: column;
  animation: animated-logo 0.5s steps(6, end) 2s 1 normal both;
}

.schools {
  padding: 10px;
}

@keyframes animated-text-education {
  from {
    width: 0;
  }
  to {
    width: 200px;
  }
}

.schoolName {
  font-weight: bold;
  font-family: century gothic;
  font-size: 20px;
  transition: color 0.5s ease;
  
  color: rgb(119, 119, 129);
}

.schoolName:hover{
  color: blue;
}

.yearActive {
  font-family: century gothic;
  font-size: 20px;
  color: rgb(119, 119, 129);
}
