@import url(https://fonts.googleapis.com/css?family=Abril+Fatface);
.app {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  min-height: 100vh;
}

body {
  background-color: aliceblue;
  padding: 0px;
  margin: 0px;
}

.aboutContainer {
  display: -webkit-flex;
  display: flex;
  font-family: century gothic;
  height: 100%;
}

a.leftButtons {
  margin: 0px;
}

.description {
  margin-top: 30px;
  width: 310px;
  height: 100px;
  text-align: justify;
  font-size: 18px;
  -webkit-animation: animated-logo 1s 4s normal both;
          animation: animated-logo 1s 4s normal both;
}

.header {
  font-family: Courier, monospace;
  display: inline-block;
}

.header {
  border-right: solid 3px rgb(10, 231, 180);
  white-space: nowrap;
  overflow: hidden;
  font-family: monospace;
  font-size: 40px;
  color: (247, 229, 229, 0.7);
}

/* Animation */
.header {
  -webkit-animation: animated-text 3s steps(14, end) 0.5s 1 normal both,
    animated-cursor 400ms steps(14, end) infinite;
          animation: animated-text 3s steps(14, end) 0.5s 1 normal both,
    animated-cursor 400ms steps(14, end) infinite;
}

/* text animation */

@-webkit-keyframes animated-text {
  from {
    width: 0;
  }
  to {
    width: 310px;
  }
}

@keyframes animated-text {
  from {
    width: 0;
  }
  to {
    width: 310px;
  }
}

/* cursor animations */

@-webkit-keyframes animated-cursor {
  from {
    border-right-color: rgb(10, 231, 180);
  }
  to {
    border-right-color: transparent;
  }
}

@keyframes animated-cursor {
  from {
    border-right-color: rgb(10, 231, 180);
  }
  to {
    border-right-color: transparent;
  }
}

.rightComponent {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-animation: animated-logo 1s 5s normal both;
          animation: animated-logo 1s 5s normal both;
}

.leftComponent {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  width: 60%;
}

.buttonContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-animation: animated-logo 1s 5s normal both;
          animation: animated-logo 1s 5s normal both;
}

.leftButtons {
  text-decoration: none;
  text-align: center;
  -webkit-align-content: center;
          align-content: center;
  margin: 20px 20px 10px 0px;
  padding: 5px;
  width: 120px;
  height: 30px;
  font-size: 18px;
  border: none;
  font-family: century gothic;
  box-shadow: 2px 2px gray;
  background-color: rgb(178, 178, 235);
  color: white;
}

.leftButtons:hover {
  border: solid 1px rgb(178, 178, 235);
  box-shadow: 2px 2px rgb(178, 178, 235);
  background-color: white;
  color: rgb(178, 178, 235);
  cursor: pointer;
}

.mailMe {
  font-family: "Courier New", Courier, monospace;
  font-size: 15px;
  padding-top: 10px;
  margin: 0px;
  white-space: nowrap;
  overflow: hidden;
  font-family: monospace;
}

.logoContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-top: 50px;
}

.anotherLogoContainer {
  position: relative;
  margin: 0px;
  -webkit-animation: animated-logo 0.5s 1s normal both;
          animation: animated-logo 0.5s 1s normal both;
}

.skillsLogo {
  display: block;
  width: 150px;
  height: 150px;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: opacity ease-out 200ms;
}

.skillsText {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  opacity: 0;
  transition: opacity ease-out 250ms;
  font-size: 40px;
  font-family: century gothic;
  font-weight: bold;
}

.skillsLogo:hover {
  opacity: 0.5;
  cursor: pointer;
  -webkit-animation: animated-skill-logo 1s;
          animation: animated-skill-logo 1s;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  transition: 0.5s;
}

.anotherLogoContainer:hover .skillsText {
  opacity: 1;
}

.skillsHeader {
  font-family: Courier, monospace;
  display: inline-block;
  /*---*/
  border-right: solid 3px rgb(10, 231, 180);
  white-space: nowrap;
  overflow: hidden;
  font-family: monospace;
  font-size: 40px;
  color: (247, 229, 229, 0.7);
  /*animation*/
  -webkit-animation: animated-text-skills 1s steps(6, end) 0s 1 normal both,
    animated-cursor 400ms steps(6, end) infinite;
          animation: animated-text-skills 1s steps(6, end) 0s 1 normal both,
    animated-cursor 400ms steps(6, end) infinite;
}

@-webkit-keyframes animated-text-skills {
  from {
    width: 0;
  }
  to {
    width: 135px;
  }
}

@keyframes animated-text-skills {
  from {
    width: 0;
  }
  to {
    width: 135px;
  }
}

@-webkit-keyframes animated-logo {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes animated-logo {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

